
.footer {
	position: fixed;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 49px;
	background: #FFFFFF;
	z-index: 9999;
	border-top: 1px solid #e6e6e6;
}
.footer ul {
	width: 100%;
	height: 100%;
}
.footer ul img {
	width: 18px;
	display: block;
	height: auto;
	margin-left: 4px;
	margin-top: 4px;
	margin-bottom: 4px;
}
.footer ul label {
	margin: 0;
	font-weight: 400;
	font-size: 12px;
	color: #000;
}
.footer ul li img:first-child {
	display: none;
}
.footer ul li.active img:first-child {
	display: block;
}
.footer ul li.active img:nth-of-type(2) {
	display: none;
}
.footer ul li.active label {
	color: #fb9600;
}



