
.MyForm .userNameLi{
     background: url(../../../static/images/regin-03.png) no-repeat 20px center;
     background-size: 16px auto;
}
.login_box{
     position: absolute;
     top: 0;
     left: 0;
     bottom: 0;
     right: 0;
     background: url('../../../static/images/login_back.png') no-repeat;
     background-position: center center;
     background-size: cover;
}
.logoImg{
   width: 100%;
   margin-top:50px;
}
.logoImg div{
     width: 85px;
     height: 90px;
     margin: 0 auto;
     background: url('../../../static/images/regin-04.png') no-repeat center center;
     background-size: 100% 100%;
}
.MyForm ul{
 width: 73.6%;
}
.MyForm li{
       border: 1px solid #FFF;
       border-radius: 17px;
       height: 34px;
       width: 100%;
       max-height: 50px;
       background-size: 100% 100%;
       margin-bottom:20px;
       position: relative;
       color: #fff;
}
.MyForm li input{
       width: 65%;
       display: block;
       height: 80%;
       border: 0;
       outline: 0;
       background: rgba(0,0,0,0);
       color: #fff;
}
.MyForm .loginBtn{
         border: none;
         background: rgba(255,186,0,.8);
         margin-bottom: .1rem;
         overflow: hidden;
         box-pack: center;
         -moz-box-pack: center;
         -ms-box-pack: center;
         -o-box-pack: center;
         color: #FFF;
         font-size: .2rem;
}
.MyForm .forgetBtn{
         background: 0;
         border: 0;
         box-align: center;
         -moz-box-align: center;
         -ms-box-align: center;
         -o-box-align: center;
}
   



